import React from "react";
import './publications.module.css';
import classes from './publications.module.css'
import main from "../Main/main.module.css";
import {IDateParents} from "../ContentForParents/ContentForParents";
import {v4} from "uuid";
import {Button, Tooltip} from "antd";
import Link from "antd/es/typography/Link";

export const Publications = () => {
    return (
        <div className={classes.wrapContainer}>
            <div style={{width: '50%'}}>
                <div className={classes.title}>Мои Публикации</div>
                <div className={classes.publications}>
                    {publications.map((publication) =>
                        <div className={classes.publication} key={publication.id}>
                            <div className={classes.publicationName}>{publication.title}</div>
                            <div className={classes.dataText}>{publication.data}</div>
                            {/*<div className={classes.linkSite}>{publication.linkSite}</div>*/}
                            <Tooltip title={publication.linkSite} color="#ffd4c1">
                                <Button size="small" className={classes.button} type="primary"><a
                                    href={publication.link}>Перейти к
                                    публикации</a>
                                </Button>
                            </Tooltip>

                        </div>
                    )}
                </div>
            </div>
            <div>
                <div className={classes.title}>Материалы для занятий</div>
                <div className={classes.publications}>
                    {scenarios.map((publication) =>
                        <div className={classes.publication} key={publication.id}>
                            <div className={classes.publicationName}>{publication.title}</div>
                            <div className={classes.dataText}>{publication.data}</div>
                            {/*<div className={classes.linkSite}>{publication.linkSite}</div>*/}
                            <Tooltip title={publication.linkSite} color="#ffd4c1">
                                <Button size="small" className={classes.button} type="primary"><a href={publication.link}>Перейти к
                                    публикации</a>
                                </Button>
                            </Tooltip>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}

interface IPublications extends IDateParents {
    linkSite: string,
    link: string,
    data?: string
}

